<template>
    <v-container v-if="loading">
        <div class="text-xs-center">
            <v-progress-circular
                indeterminate
                :size="150"
                :width="8"
                color="green"
            >
            </v-progress-circular>
        </div>
    </v-container>

    <v-container v-else>
        <v-layout wrap>
            <v-flex xs12 mr-1 ml-1>
                <v-card>
                    <v-img
                        :src="posterImage(singleMovie)"
                        aspect-ratio="2"
                    ></v-img>
                    <v-card-title primary-title>
                        <div>
                            <h2 class="headline mb-0">
                                {{ singleMovie.title }}
                            </h2>
                            <p>{{ singleMovie.release_date }}</p>
                            <p>{{ singleMovie.overview }}</p>
                            <h3>Runtime:</h3>
                            {{ singleMovie.runtime }} minutes
                            <h3>Genres:</h3>
                            <ul
                                v-for="(item, index) in movieGenres"
                                :key="index"
                            >
                                <li>{{ item.name }}</li>
                            </ul>
                        </div>
                    </v-card-title>
                    <v-card-actions>
                        <v-btn text color="green" @click="back">back</v-btn>
                    </v-card-actions>
                    <!--if the user logged in-->
                    <div v-if="loggedIn">
                        <v-card-actions>
                            <v-btn
                                v-if="gottaWatched"
                                text
                                color="green"
                                @click="removeGottaWatch"
                                >UnGottaWatch</v-btn
                            >
                            <v-btn
                                v-else
                                text
                                color="green"
                                @click="addToGottaWatchList(movie)"
                                >GottaWatch</v-btn
                            >
                        </v-card-actions>
                        <v-card-actions>
                            <v-btn
                                v-if="watched"
                                text
                                color="green"
                                @click="removeWatched"
                                >UnWatched</v-btn
                            >
                            <v-btn v-else text color="green" @click="addWatched"
                                >Watched</v-btn
                            >
                        </v-card-actions>
                    </div>
                    <!--if the user did not log in-->
                    <div v-else>
                        <v-card-actions>
                            <v-btn text color="green">
                                <!-- TODO: get tooltips to work -->
                                <v-tooltip
                                    light
                                    content="Login to add this movie to your gotta watch list"
                                >
                                    GottaWatch
                                </v-tooltip>
                            </v-btn>
                        </v-card-actions>
                        <v-card-actions>
                            <v-btn text color="green">
                                <v-tooltip
                                    light
                                    content="Login to add this movie to your watched list"
                                >
                                    Watched
                                </v-tooltip>
                            </v-btn>
                        </v-card-actions>
                    </div>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import movieApi from '@/MovieApi';

export default {
    props: ['id'],
    data() {
        return {
            singleMovie: '',
            movieGenres: [],
            gottaWatchMovies: [],
            watchedMovies: [],
            dialog: false,
            loading: true,
            ratings: ''
        };
    },

    mounted() {
        movieApi
            .fetchSingleMovie(this.id)
            .then(response => {
                this.singleMovie = response;
                this.ratings = this.singleMovie.vote_average;
                this.movieGenres = this.singleMovie.genres;
                this.loading = false;
            })
            .catch(error => {
                // eslint-disable-next-line no-console
                console.error('AJAX QUERY ERROR', error);
            });
    },

    computed: {
        loggedIn() {
            return this.$store.state.isAuthenticated;
        },
        gottaWatchList(){
            return this.$store.state.gottaWatchList;
        },
        gottaWatched() {
            return this.gottaWatchMovies.some(
                movie => movie.id === this.singleMovie.id
            );
        },
        watched() {
            return this.watchedMovies.some(
                movie => movie.id === this.singleMovie.id
            );
        }
    },

    methods: {
        back() {
            this.$router.push('/');
        },

        addToGottaWatchList(movie) {
            this.$store.dispatch('addToGottaWatch', movie);
        },

        removeGottaWatch() {
            let result = this.gottaWatchMovies.find(
                movie => movie.id === this.singleMovie.id
            );

            this.gottaWatchMovies.splice(result, 1);
        },

        addWatched() {
            this.watchedMovies.push(this.singleMovie);
        },

        removeWatched() {
            let result = this.watchedMovies.find(
                movie => movie.id === this.singleMovie.id
            );

            this.watchedMovies.splice(result, 1);
        },

        posterImage(item) {
            let baseImgUrl = 'https://image.tmdb.org/t/p/';
            return baseImgUrl.concat('w154', item.poster_path);
        }
    },

    watch: {
        gottaWatchMovies: {
            handler: function(newCollection) {
                // store in local storage for later
                localStorage.setItem(
                    'gottaWatchMovies',
                    JSON.stringify(newCollection)
                );
            }
        },
        watchedMovies: {
            handler: function(newCollection) {
                // store in local storage for later
                localStorage.setItem(
                    'watchedMovies',
                    JSON.stringify(newCollection)
                );
            }
        }
    }
};
</script>
